<template>
  <div>
    <div class="postBlock">
      <div class="postContent">
        <h3>{{ title }} -- {{ user }} ({{ getDate(timeCreated) }})</h3>
        <p>{{ content }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "postComponent",
  props: {
    title: String,
    content: String,
    user: String,
    timeCreated: Number,
  },
  methods: {
    getDate(timestamp) {
      return new Date(Number(timestamp)).toLocaleDateString();
    },
  },
};
</script>

<style scoped>
.postBlock {
  background: var(--light-second-color);
  border-radius: 10px;
}

.postContent {
  margin: 10px;
  width: max-content;
}
</style>