import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    allPages: ["Home", "Login"],
    pageState: "Home",
    availableForums: [],
    currentForum: "",
    forumPosts: [],
    isAddingPost: false,
    isLoggedIn: false,
    currentUserName: "",
    currentUserPFP: ""
  },
  mutations: {
    switchPage(state, newPage) {
      state.pageState = newPage

    },
    setForums(state, forumArray) {
      state.availableForums = forumArray
    },
    setForumPosts(state, postArray) {
      state.forumPosts = postArray
    },
    setCurrentForum(state, newForum) {
      state.currentForum = newForum
    },
    setPostStatus(state, newStatus) {
      state.isAddingPost = newStatus
    },
    userLoggedIn(state, userObject) {
      state.isLoggedIn = true
      state.currentUserName = userObject['name']
      state.currentUserPFP = userObject['pfp']
    },
    logout(state) {
      state.isLoggedIn = false
      state.currentUserName = ""
      state.currentUserPFP = ""
      window.location.href = "/"
    }
  },
  actions: {
    setCurrentForumData({ commit }, newForumData) {
      commit('switchPage', "forum")
      commit('setCurrentForum', newForumData[0])
      commit('setForumPosts', newForumData[1])
    },
    updateCurrentForum({ commit }) {
      fetch(`/api/forum/${this.state.currentForum}`, {
        method: "POST",
      })
        .then(res => res.json())
        .then(newForumData => {
          console.log(newForumData);
          commit('switchPage', "forum")
          // commit('setCurrentForum', newForumData[0])
          commit('setForumPosts', newForumData)
        })
    }
  },
  modules: {},
  getters: {
    pageState(state) {
      return state.pageState;
    },
    forums(state) { return state.availableForums },
    getIsPosting(state) { return state.isAddingPost }
  },
});
